import { Button, Modal } from 'antd';
import React, { useEffect } from 'react';
import { currencyFormat } from '../../../utils/helper/utilities';
import usePayment from '../hooks/usePayment';

export default function ModalCashChange(props) {
	const ref = React.createRef();

	const { handlePaymentStore = (e) => {}, state = {}, handleCancel = () => {}, open = false } = props;
	const { getCashChange } = usePayment();

	const handleFocus = () => {
		if (ref && ref.current && ref.current !== null) {
			ref.current.focus();
		}
	};

	const handleClick = (e) => {
		e.preventDefault();
		if (document.activeElement === ref.current && e.target.id !== 'tender-media') {
			handlePaymentStore(e);
			handleCancel();
			return;
		}
	};

	const onKeyPress = (e) => {
		const btn = ref && ref.current && ref.current !== null && ref.current;

		handleFocus();
		if (e.key === 'Enter' && document.activeElement === btn) {
			handleClick(e);
			return;
		}
	};

	useEffect(() => {
		handleFocus();

		document.addEventListener('keydown', onKeyPress);
		return () => {
			document.addEventListener('keydown', onKeyPress);
		};
	}, [ref]);

	return (
		<Modal centered width={450} footer={false} onCancel={handleCancel} open={open} bodyStyle={{ backgroundColor: '#f8f8f8' }}>
			<div className="w-full h-48 flex items-center justify-center flex-col gap-3">
				<h1 className="text-lg font-medium text-gray-700">KEMBALIAN PELANGGAN</h1>
				<p className="text-3xl font-bold">Rp {currencyFormat(getCashChange(state?.settles, state?.cashPaid), 0)}</p>

				<Button autoFocus ref={ref} className={`w-1/2 btn-page h-14 mt-7`} onClick={handleClick}>
					PRINT BILL
				</Button>
			</div>
		</Modal>
	);
}
